.ui-prompt {
  width: 300px; }
  .ui-prompt-text {
    font-size: 12px;
    margin-bottom: 8px; }
  .ui-prompt-input {
    font-size: 12px;
    width: 100%;
    box-sizing: border-box;
    padding: 6px;
    border-radius: 2px;
    border: 1px solid #ccc;
    background-color: #fff;
    outline: none; }
    .ui-prompt-input::-ms-clear {
      display: none; }
    .ui-prompt-input:focus {
      box-shadow: 0px 0px 2px .5px #38f; }



