.ui-toast-mask {
  width: 100%;
  position: fixed;
  top: 0;
  bottom: 0;
  z-index: 100; }
  .ui-toast-mask.black {
    background-color: rgba(0, 0, 0, 0.5); }
  .ui-toast-mask.white {
    background-color: rgba(255, 255, 255, 0.5); }

.ui-toast {
  min-width: 140px;
  padding: 0 20px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, 50%);
  color: #fff;
  font-size: 14px;
  text-align: center;
  line-height: 26px;
  height: 26px;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 3px; }



