/* ====================== alert style =============================*/
.ui-alert-mask {
  width: 100%;
  position: fixed;
  top: 0;
  bottom: 0;
  z-index: 100; }
  .ui-alert-mask.black {
    background-color: rgba(0, 0, 0, 0.5);
    border: none; }
  .ui-alert-mask.white {
    background-color: rgba(255, 255, 255, 0.5); }

.uialert-mask .ui-alert {
  border: 1px solid #eee; }

.uialert-mask.black .ui-alert {
  border: none; }

.ui-alert {
  min-width: 278px;
  text-align: left;
  position: absolute;
  left: 50%;
  top: 50%;
  border-radius: 5px;
  background-color: #fff;
  transform: translate(-50%, -53%); }
  .ui-alert-head {
    padding: 14px 20px;
    border-bottom: 1px solid #e7e7e7;
    font-size: 16px;
    word-break: break-all; }
  .ui-alert-body {
    padding: 10px 20px 16px 20px;
    font-size: 14px;
    color: #666; }
  .ui-alert-footer {
    text-align: right;
    padding: 16px 20px 14px 20px; }
  .ui-alert-but {
    padding: 0 15px;
    font-size: 12px;
    height: 26px;
    cursor: pointer;
    border-radius: 4px;
    outline: none; }
  .ui-alert-confirm {
    border: 1px solid #4794ff;
    background-color: #4794ff;
    color: #fff; }
    .ui-alert-confirm:hover {
      background-color: #4087ea; }
  .ui-alert-cancel {
    border: 1px solid #ccc;
    background-color: #fff;
    color: #666; }
    .ui-alert-cancel:hover {
      background-color: #f0f0f0; }



